<template>
  <v-card
    height="100%"
  >
    <div
      id="nodocentes-unidadgestion"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Cargos habilitados y liquidados por Unidad de Gestión.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('nodocentes-unidadgestion')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as No Docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Unidad Académica
                </th>
                <th class="text-left">
                  Cargos habilitados
                </th>
                <th class="text-left">
                  Cargos liquidados
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in unidadAcademicaData"
                :key="index"
              >
                <td>{{ parseUnidadAcademica(index) }}</td>
                <td>{{ item.habilitados }}</td>
                <td>{{ item.liquidados }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['nodocentesCargos', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 300,
          right: 20,
          top: 20,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 400,
        legend: {
          position: 'none',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        isStacked: true,
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Unidad Académica', label: 'UnidadAcademica', type: 'string' },
          { id: 'Habilitados', label: 'Habilitados', type: 'number' },
          { id: 'Liquidados', label: 'Liquidados', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'ICI' },
              { v: this.unidadAcademicaData.ICI.habilitados, f: this.unidadAcademicaData.ICI.habilitados },
              { v: this.unidadAcademicaData.ICI.liquidados, f: this.unidadAcademicaData.ICI.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDH' },
              { v: this.unidadAcademicaData.IDH.habilitados, f: this.unidadAcademicaData.IDH.habilitados },
              { v: this.unidadAcademicaData.IDH.liquidados, f: this.unidadAcademicaData.IDH.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDEI' },
              { v: this.unidadAcademicaData.IDEI.habilitados, f: this.unidadAcademicaData.IDEI.habilitados },
              { v: this.unidadAcademicaData.IDEI.liquidados, f: this.unidadAcademicaData.IDEI.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'ICO' },
              { v: this.unidadAcademicaData.ICO.habilitados, f: this.unidadAcademicaData.ICO.habilitados },
              { v: this.unidadAcademicaData.ICO.liquidados, f: this.unidadAcademicaData.ICO.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. Académica' },
              { v: this.unidadAcademicaData.ACAD.habilitados, f: this.unidadAcademicaData.ACAD.habilitados },
              { v: this.unidadAcademicaData.ACAD.liquidados, f: this.unidadAcademicaData.ACAD.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. de Cultura y Medios' },
              { v: this.unidadAcademicaData.CULT.habilitados, f: this.unidadAcademicaData.CULT.habilitados },
              { v: this.unidadAcademicaData.CULT.liquidados, f: this.unidadAcademicaData.CULT.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. de Investigación' },
              { v: this.unidadAcademicaData.INVE.habilitados, f: this.unidadAcademicaData.INVE.habilitados },
              { v: this.unidadAcademicaData.INVE.liquidados, f: this.unidadAcademicaData.INVE.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. Legal y Técnica' },
              { v: this.unidadAcademicaData.SLYT.habilitados, f: this.unidadAcademicaData.SLYT.habilitados },
              { v: this.unidadAcademicaData.SLYT.liquidados, f: this.unidadAcademicaData.SLYT.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Vicerrectorado' },
              { v: this.unidadAcademicaData.VICE.habilitados, f: this.unidadAcademicaData.VICE.habilitados },
              { v: this.unidadAcademicaData.VICE.liquidados, f: this.unidadAcademicaData.VICE.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. Administrativa' },
              { v: this.unidadAcademicaData.ADMI.habilitados, f: this.unidadAcademicaData.ADMI.habilitados },
              { v: this.unidadAcademicaData.ADMI.liquidados, f: this.unidadAcademicaData.ADMI.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. General' },
              { v: this.unidadAcademicaData.GRAL.habilitados, f: this.unidadAcademicaData.GRAL.habilitados },
              { v: this.unidadAcademicaData.GRAL.liquidados, f: this.unidadAcademicaData.GRAL.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Auditoría Interna' },
              { v: this.unidadAcademicaData.AUDI.habilitados, f: this.unidadAcademicaData.AUDI.habilitados },
              { v: this.unidadAcademicaData.AUDI.liquidados, f: this.unidadAcademicaData.AUDI.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. de Desarrollo Tecnológico y Social' },
              { v: this.unidadAcademicaData.SERV.habilitados, f: this.unidadAcademicaData.SERV.habilitados },
              { v: this.unidadAcademicaData.SERV.liquidados, f: this.unidadAcademicaData.SERV.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Sec. de Planeamiento y Desarrollo Universitario' },
              { v: this.unidadAcademicaData.DESA.habilitados, f: this.unidadAcademicaData.DESA.habilitados },
              { v: this.unidadAcademicaData.DESA.liquidados, f: this.unidadAcademicaData.DESA.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Escuela Secundaria' },
              { v: this.unidadAcademicaData.ACSE.habilitados, f: this.unidadAcademicaData.ACSE.habilitados },
              { v: this.unidadAcademicaData.ACSE.liquidados, f: this.unidadAcademicaData.ACSE.liquidados }
            ]
          },
          {
            c: [
              { v: 0, f: 'Rectorado' },
              { v: this.unidadAcademicaData.RECT.habilitados, f: this.unidadAcademicaData.RECT.habilitados },
              { v: this.unidadAcademicaData.RECT.liquidados, f: this.unidadAcademicaData.RECT.liquidados }
            ]
          }
        ]
      }
    },
    unidadAcademicaData () {
      // "ICI"
      // "IDH"
      // "IDEI"
      // "ICO"
      // "ACAD"
      // "CULT"
      // "INVE"
      // 'UByD' biblioteca depende de INVE
      // "SLYT"
      // "VICE"
      // "ADMI"
      // "GRAL"
      // "AUDI"
      // "SERV"
      // "DESA"
      // "SEGE"
      // "ACSE"
      // "RECT"
      return this.nodocentesCargos
        .reduce(
          (acc, cur) => {
            const ICI = cur.unidad_academica === 'ICI'
            const IDH = cur.unidad_academica === 'IDH'
            const IDEI = cur.unidad_academica === 'IDEI'
            const ICO = cur.unidad_academica === 'ICO'
            const ACAD = cur.unidad_academica === 'ACAD'
            const CULT = cur.unidad_academica === 'CULT'
            const INVE = cur.unidad_academica === 'INVE' || cur.unidad_academica === 'UByD'
            const SLYT = cur.unidad_academica === 'SLYT'
            const VICE = cur.unidad_academica === 'VICE'
            const ADMI = cur.unidad_academica === 'ADMI'
            const GRAL = cur.unidad_academica === 'GRAL' || cur.unidad_academica === 'SEGE'
            const AUDI = cur.unidad_academica === 'AUDI'
            const SERV = cur.unidad_academica === 'SERV'
            const DESA = cur.unidad_academica === 'DESA'
            const ACSE = cur.unidad_academica === 'ACSE'
            const RECT = cur.unidad_academica === 'RECT'
            const subr1 = new RegExp('SubR: Cat 1', 'gm')
            const subr2 = new RegExp('SubR: Cat 2', 'gm')
            const subr3 = new RegExp('SubR: Cat 3', 'gm')
            const subr4 = new RegExp('SubR: Cat 4', 'gm')
            const subr5 = new RegExp('SubR: Cat 5', 'gm')
            const subr6 = new RegExp('SubR: Cat 6', 'gm')
            const subrogado = subr1.test(cur.subrogancia) || subr2.test(cur.subrogancia) || subr3.test(cur.subrogancia) || subr4.test(cur.subrogancia) || subr5.test(cur.subrogancia) || subr6.test(cur.subrogancia)
            const parseHabilitadosLiquidados = ua => subrogado
              ? { habilitados: ua.habilitados, liquidados: ua.liquidados + 1 }
              : { liquidados: ua.liquidados, habilitados: ua.habilitados + 1 }
            return ICI
              ? { ...acc, ICI: parseHabilitadosLiquidados(acc.ICI) }
              : IDH
                ? { ...acc, IDH: parseHabilitadosLiquidados(acc.IDH) }
                : IDEI
                  ? { ...acc, IDEI: parseHabilitadosLiquidados(acc.IDEI) }
                  : ICO
                    ? { ...acc, ICO: parseHabilitadosLiquidados(acc.ICO) }
                    : ACAD
                      ? { ...acc, ACAD: parseHabilitadosLiquidados(acc.ACAD) }
                      : CULT
                        ? { ...acc, CULT: parseHabilitadosLiquidados(acc.CULT) }
                        : INVE
                          ? { ...acc, INVE: parseHabilitadosLiquidados(acc.INVE) }
                          : SLYT
                            ? { ...acc, SLYT: parseHabilitadosLiquidados(acc.SLYT) }
                            : VICE
                              ? { ...acc, VICE: parseHabilitadosLiquidados(acc.VICE) }
                              : ADMI
                                ? { ...acc, ADMI: parseHabilitadosLiquidados(acc.ADMI) }
                                : GRAL
                                  ? { ...acc, GRAL: parseHabilitadosLiquidados(acc.GRAL) }
                                  : AUDI
                                    ? { ...acc, AUDI: parseHabilitadosLiquidados(acc.AUDI) }
                                    : SERV
                                      ? { ...acc, SERV: parseHabilitadosLiquidados(acc.SERV) }
                                      : DESA
                                        ? { ...acc, DESA: parseHabilitadosLiquidados(acc.DESA) }
                                        : ACSE
                                          ? { ...acc, ACSE: parseHabilitadosLiquidados(acc.ACSE) }
                                          : RECT
                                            ? { ...acc, RECT: parseHabilitadosLiquidados(acc.RECT) }
                                            : acc
          },
          {
            ICI: { habilitados: 0, liquidados: 0 },
            IDH: { habilitados: 0, liquidados: 0 },
            IDEI: { habilitados: 0, liquidados: 0 },
            ICO: { habilitados: 0, liquidados: 0 },
            ACAD: { habilitados: 0, liquidados: 0 },
            CULT: { habilitados: 0, liquidados: 0 },
            INVE: { habilitados: 0, liquidados: 0 },
            SLYT: { habilitados: 0, liquidados: 0 },
            VICE: { habilitados: 0, liquidados: 0 },
            ADMI: { habilitados: 0, liquidados: 0 },
            GRAL: { habilitados: 0, liquidados: 0 },
            AUDI: { habilitados: 0, liquidados: 0 },
            SERV: { habilitados: 0, liquidados: 0 },
            DESA: { habilitados: 0, liquidados: 0 },
            ACSE: { habilitados: 0, liquidados: 0 },
            RECT: { habilitados: 0, liquidados: 0 }
          }
        )
    }
  },
  methods: {
    parseUnidadAcademica (v) {
      return v === 'ICI'
        ? 'ICI'
        : v === 'IDH'
          ? 'IDH'
          : v === 'IDEI'
            ? 'IDEI'
            : v === 'ICO'
              ? 'ICO'
              : v === 'ACAD'
                ? 'Secretaría Académica'
                : v === 'CULT'
                  ? 'Secretaría de Cultura y Medios'
                  : v === 'INVE'
                    ? 'Secretaría de Investigación'
                    : v === 'SLYT'
                      ? 'Secretaría Legal y Técnica'
                      : v === 'VICE'
                        ? 'Vicerrectorado'
                        : v === 'ADMI'
                          ? 'Secretaría Administración'
                          : v === 'GRAL'
                            ? 'Secretaría General'
                            : v === 'AUDI'
                              ? 'Auditoría Interna'
                              : v === 'SERV'
                                ? 'Secretaría de Desarrollo Tecnológico y Social'
                                : v === 'DESA'
                                  ? 'Secretaría de Planeamiento y Desarrollo Universitario'
                                  : v === 'ACSE'
                                    ? 'Escuela Secundaria'
                                    : v === 'RECT'
                                      ? 'Rectorado'
                                      : v
    }
  }
}
</script>
